.tweet-body {
    display: flex;
    flex-direction: column;
    width: 100%;
    height: 170px;
    box-shadow: 0 2px 4px 0 rgba(0, 0, 0, 0.5);
    background-color: #15202a;
    /* margin-left: 25rem; */
    margin-bottom: 0.1rem;
}

.tweet-body:hover {
    background-color: #11171d;
    border: #ffffff;
}

.picture {
    margin-left: 24px;
    margin-top: 17px;
    border-radius: 50%;
    width: 48px;
    height: 48px;
}

.name {
    display: flex;
    flex-direction: row;
    margin-left: 15px;
    margin-top: 19px;
    min-width: 85px;
    height: 17px;
    font-family: HelveticaNeue;
    font-size: 14px;
    font-weight: 500;
    font-style: normal;
    font-stretch: normal;
    line-height: normal;
    letter-spacing: -0.6px;
    color: #ffffff;
}

.handle {
    margin-left: 15px;
    margin-top: 19px;
    min-width: 128px;
    height: 16px;
    font-family: HelveticaNeue;
    font-size: 14px;
    font-weight: normal;
    font-style: normal;
    font-stretch: normal;
    line-height: normal;
    letter-spacing: -0.6px;
    color: #75797d;
}

.tweet {
    margin-left: 15px;
    margin-top: 7px;
    width: 100%;
    height: 88px;
    font-family: HelveticaNeue;
    font-size: 14px;
    font-weight: 300;
    font-style: normal;
    font-stretch: normal;
    line-height: normal;
    letter-spacing: -0.6px;
    color: #ffffff;
}
/* font faces */
@font-face {
  font-family: "basefont-regular";
  src: local("basefont-regular"),
    url(/swarm_construction_dashboard/static/media/CircularStd-Book.6365c40a.otf) format("opentype");
}

@font-face {
  font-family: "basefont-medium";
  src: local("basefont-medium"),
    url(/swarm_construction_dashboard/static/media/CircularStd-Medium.4fcdd97f.otf) format("opentype");
}

@font-face {
  font-family: "basefont-bold";
  src: local("basefont-bold"),
    url(/swarm_construction_dashboard/static/media/CircularStd-Bold.6baed2bf.otf) format("opentype");
}

/* typography styles */
.is-dark-text {
  color: rgb(67, 68, 86);
}

.is-dark-text-light {
  color: rgb(128, 145, 171);
}

.is-light-text {
  /* color: rgb(193, 204, 222); */
  color: rgb(255, 255, 255);
}

/* font weights */

.font-medium {
  font-family: "basefont-medium";
}

.font-bold {
  font-family: "basefont-bold";
}

/* text sizes */
.text-x-large {
  font-size: 2rem;
}

.text-large {
  font-size: 1.25rem;
}

.text-medium {
  font-size: 1.125rem;
}

.text-small {
  font-size: 0.875rem;
}

.text-x-small {
  font-size: 0.75rem;
}

/* letter spacing */
.letter-spacing {
  letter-spacing: 0.05rem;
}

.chart-container svg text {
  letter-spacing: 0.05rem;
}

body {
  background: rgb(23, 32, 46) !important;
  font-family: "basefont-regular" !important;
  color: rgb(255, 255, 255);
  padding-top: 125px;
}

.container-fluid {
  width: 95% !important;
}

/* navbar styles */
.is-white {
  background: rgb(255, 255, 255);
  height: 65px;
}

.is-dark {
  background: rgb(19, 25, 36);
  height: 60px;
}

.nav-secondary {
  margin-top: 65px;
  box-shadow: 0px 2px 10px #0000006e;
}

/* user image style */
.img-container>img {
  width: 40px;
}

.grid-card {
  display: flex;
  flex-grow: 1;
  flex-direction: column;
  display: flex;
  flex-grow: 1;
  flex-direction: column;
  padding: 1.5rem;
  height: calc(100%);
}

.chart-card {
  display: flex;
  flex-grow: 1;
  flex-direction: column;
  display: flex;
  flex-grow: 1;
  flex-direction: column;
  padding: 1.5rem 1.5rem 0.75rem 1.5rem;
  height: calc(100%);
}

.card-heading {
  display: flex;
  align-items: stretch;
  justify-content: space-between;
  position: relative;
  min-height: 30px;
}

.is-card-dark {
  background: rgb(32, 42, 59) !important;
}

.logo-adjust {
  margin-top: -4px;
}

.col-md-8.border-left {
  border-left: 1px solid rgba(222, 226, 230, 0.07) !important;
}

.fab {
  color: rgb(255, 255, 255);
  opacity: 0.6;
}

.conv>span {
  display: inline-block;
  min-width: 75px !important;
}

.chart-container>* {
  height: 100% !important;
  /* CHANGE ME TO ADJUST MAX HEIGHT OF CONTAINERS */
  /* max-height: 350px; */
}

.full-height.border-left,
.full-height.border-right {
  border-left: 1px solid rgba(255, 255, 255, 0.1) !important;
  border-right: 1px solid rgba(255, 255, 255, 0.1) !important;
}

.custom-dropdown {
  border-radius: 4px;
  background: transparent;
  border: 1px solid rgba(193, 204, 222, 0.2);
  font-family: "basefont-regular";
}

.Dropdown-menu {
  max-height: 300px;
}

.Dropdown-control {
  border: none;
  color: #c1ccde;
  background: transparent;
}

.Dropdown-arrow {
  top: 18px;
}

/* media queries */
@media screen and (max-width: 991px) {

  .container-fluid.pr-5,
  .container-fluid.pl-5 {
    width: 90% !important;
    padding-left: 0 !important;
    padding-right: 0 !important;
  }

  .scrollable {
    overflow: scroll;
  }

  .scrollable-x {
    overflow-x: scroll;
  }

  .behavior_extra {
    margin-top: 2%;
  }

  .blueprint_card {}
}

@media screen and (min-width: 991px) {
  .full-height {
    height: 100% !important;
  }

  .scrollable {
    overflow: scroll;
  }

  .scrollable-x {
    overflow-x: scroll;
  }

  .behavior_extra {
    margin-top: 2%;
  }

  .blueprint_card {}
}

@media screen and (max-width: 567px) {

  .full-height.border-left,
  .full-height.border-right {
    border-left: none !important;
    border-right: none !important;
  }

  .chart-container {
    height: 180px !important;
  }

  .large {
    height: 350px !important;
  }

  .scrollable {
    overflow: scroll;
  }

  .scrollable-x {
    overflow-x: scroll;
  }

  .behavior_extra {
    margin-top: 10%;
  }

  .blueprint_card {}
}

.behavior {
  /* color: white; */
  /* background-color: white; */
  padding: 5px;
  border-radius: 8px;
  display: inline-block;
  /* border: 1px solid red; */
  background: #b5d5ef;
  color: #002a50;
  transition: all 0.15s;
  -webkit-transform: translateY(-5px);
  transform: translateY(-5px);
  box-shadow: inset 0 0 0 3px #76b1e1, 0 3px 6px rgba(0, 0, 0, 0.16),
    0 3px 6px rgba(0, 0, 0, 0.23);
  box-shadow: 0 0 0 5px #4c99d8;
  border-color: #94a0b4;
}

.behavior_container {
  max-height: 300px;
  margin-bottom: 20px;
}

.selected_behavior {
  box-shadow: 0 0 0 5px #2d7b19;
  background: #55e85a;
}

